<template>
  <div class="dialog-container">
    <el-dialog title="" width="1000px" top="30px" :visible.sync="visible" :close-on-click-modal="false" @close="$emit('update:show', false)"
      :show="show">
      <div class="demand">
        <el-form style="width:950px;margin:0 auto" class="demo-ruleForm" :model="demandData" :inline="true" size="mini"
          label-width="135px">
          <el-form-item label="" required>
            <el-input size="mini" type="text" :style="{ width: 800 + 'px' }" v-model="demandData.xmmc" readonly
              placeholder="">
              <el-button :disabled="Type == 'disabled'" slot="append" icon="el-icon-circle-plus-outline" style="width:30px;padding:0 12px;"
                @click="adddemandtItem"></el-button>
            </el-input>
          </el-form-item>
          <el-form-item label="" required>
            <el-input size="mini" type="text" style="width:325px" v-model="xmzt" readonly></el-input>
          </el-form-item>
          <!--
              :disabled="Type == 'editDisabled'"
           -->
          <el-form-item label="" required>
            <el-select v-model="demandData.xqfl" size="mini" placeholder="" style="width:325px">
              <el-option v-for="(item, index) in xqflList" :key="index" :label="item.mc" :value="item.label"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" required>
            <el-select v-model="teacherData" size="mini" multiple placeholder="" style="width:800px">
              <el-option v-for="(tcls, index) in tclsList" :key="index" :label="tcls.username" :value="tcls.userid+','+tcls.username"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" required>
            <el-select v-model="demandData.sfsx" size="mini" placeholder="" style="width:325px">
              <el-option v-for="(item, index) in sfxsList" :key="index" :label="item.mc" :value="item.label"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" required>
            <el-cascader size="mini" style="width:325px" v-model="val" :props="defaultProps" :options="options" @change="handleItemChange"
              :show-all-levels="false"></el-cascader>
            <!-- <div>
              <el-input
                size="mini"
                style="width:325px"
                v-model="demandData.cpbjmc"
                readonly
                ref="inputcp"
                placeholder=""
                @focus="handleShowCp"

              ></el-input>
              <div class="cptree" v-show="cpTreeShow">
                <cpTree @handleNodeClick="handleNodeClick"></cpTree>
              </div>
            </div> -->
          </el-form-item>

          <el-form-item label="CROWD" required>
            <el-select v-model="demandData.crowdywxbh" size="mini" placeholder="CROWD" style="width:325px"
              @change="handleChangeCrowdYwx">
              <el-option v-for="(ywx, index) in crowdywxList" :key="index" :label="ywx.lbmc" :value="ywx.lbdm"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="CROWD" required>
            <el-select v-model="demandData.crowdcpbh" size="mini" placeholder="CROWD" style="width:325px" @change="handleChangeCrowdCp">
              <el-option v-for="(cp, index) in crowdcpList" :key="index" :label="cp.lbmc" :value="cp.lbdm"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" required>
            <el-date-picker :picker-options="pickerBeginDateBefore" :clearable="false" size="mini" v-model="demandData.qwsjwcrq"
              type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd" style="width:325px;" @change="handleChangeQwwcrq"></el-date-picker>
          </el-form-item>

          <el-form-item label="" required>
            <el-date-picker :picker-options="pickerBeginDateBefore" :clearable="false" size="mini" v-model="demandData.qwkfjfrq"
              type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd" style="width:325px;" @change="handleChangeQwjfrq"></el-date-picker>
          </el-form-item>

          <el-form-item label="" required v-if="demandData.xqfl != 1">
            <el-input type="text" style="width: 800px" v-model="glxqMc" readonly placeholder="">
              <!--  -->
              <el-button :disabled="Type == 'disabled'||Type == 'editDisabled' || !demandData.xmbh" slot="append" icon="el-icon-circle-plus-outline"
                style="width:30px;padding:0 12px;" @click="handleRelaseDemand"></el-button>
            </el-input>
          </el-form-item>

          <el-form-item label="" required>
            <el-input size="mini" v-model="demandData.bt" type="text" placeholder="" style="width:800px;"></el-input>
          </el-form-item>

          <el-form-item label="" required>
            <uploadFile :Type="'demand'" :istb="isClearFile" @handleUploadFile="handleUploadFile"></uploadFile>
            <p class="file" v-if="!!fwznfjMc && Type.includes('edit')">
              {{fwznfjMc}}
              <i class="el-icon-close" @click="handleRemoveFwznfj"></i>
            </p>
          </el-form-item>
        </el-form>
        <div class="demand-textarea">
          <p></p>
          <div id="summernoteTT"></div>
        </div>

        <div style="text-align:right;width:100%;margin:10px 0;padding:0 20px;">
          <el-button size="small" type="primary" @click="handleCommit"></el-button>
          <el-button size="small" @click="visible = false"></el-button>
        </div>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogdemandVisible" :close-on-click-modal="false" width="800px" top="30px"
      append-to-body>
      <div style="padding:10px;">
        <itemChoose @handleEdit="handleEdit"></itemChoose>
      </div>
    </el-dialog>

    <xqlistDilog :show.sync="demandListVisible" :xmbh="demandData.xmbh" @handleChooseDemand="handleChooseDemand"></xqlistDilog>

  </div>
</template>

<script>
  import axios from "axios";
  import itemChoose from "@/components/BusinessPage/itemChoose.vue";
  import uploadFile from "@/components/BusinessPage/upload.vue";
  import {
    getProjects
  } from "@/api/xmkb.js";
  import xqlistDilog from "@/views/BusinessPage/demand/xqlist-dialog.vue";
  import cpTree from "@/views/BusinessPage/demand/cpTree.vue";

  import {
    formatTime,
    getMenu,
    getSession,
    getMyDate,
    GetDateStr
  } from "@/utils/util.js";

  export default {
    data() {
      return {
        isInnerItem: false, // 
        dialogdemandVisible: false,
        demandListVisible: false, //
        visible: this.show,
        isClearFile: false, //
        defaultProps: {
          value: "id",
          label: "title"
        },
        options: [], //
        xmmc: "",
        xmbh: "",
        xmzt: "",
        xqflList: [],
        xmcpList: [], //
        tclsList: [], //
        sfxsList: [{
            mc: "",
            label: "1"
          },
          {
            mc: "",
            label: "0"
          }
        ], //
        crowdywxList: [], //crowd
        crowdcpList: [], //crowd
        cpData: [], // 
        val: [],
        teacherData: [],
        fwznfjMc: "", //
        glxqMc: "", //
        demandData: {
          xmbh: "",
          xmmc: "",
          xqfl: "",
          teacherData: "",
          sfsx: "0",
          cpbjbh: "",
          cpbjmc: "",
          crowdywxbh: "", //crowd 
          crowdywxmc: "", //crowd 
          crowdcpbh: "", //crowd 
          crowdcpmc: "", //crowd 
          qwsjwcrq: "",
          qwkfjfrq: "",
          glxqWid: "",
          bt: "",
          xqfjwid: "",
          xqfjmc: ""
        },
        pickerBeginDateBefore: {
          disabledDate(time) {
            let curDate = new Date().getTime();
            return time.getTime() < Date.now() - 8.64e7;
          }
        }
      };
    },
    props: {
      show: {
        type: Boolean,
        default: false
      },
      demandInfo: {
        type: Object,
        default: () => {
          return {};
        }
      },
      Type: {
        type: String,
        default: ""
      },
      btnbh: {
        type: String,
        default: ""
      }
    },
    components: {
      itemChoose,
      uploadFile,
      xqlistDilog,
      cpTree
    },
    methods: {
      handleChangeQwwcrq() {
        if (!!this.demandData.qwkfjfrq && (new Date(this.demandData.qwsjwcrq) > new Date(this.demandData.qwkfjfrq))) {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'warning',
            callback: action => {}
          });
          this.demandData.qwsjwcrq = '';
        }
      },
      handleChangeQwjfrq() {
        if (!!this.demandData.qwsjwcrq && (new Date(this.demandData.qwsjwcrq) > new Date(this.demandData.qwkfjfrq))) {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'warning',
            callback: action => {}
          });
          this.demandData.qwkfjfrq = '';
        }
      },
      // 
      handleRemoveFwznfj() {
        this.fwznfjMc = "";
        this.demandData.xqfjwid = "";
        this.demandData.xqfjmc = "";
      },
      // 
      handleNodeClick(data) {
        if (!data.children && !!data.isOptional) {
          this.demandData.cpbjbh = data.id;
          this.demandData.cpbjmc = data.title;
          this.cpTreeShow = false;
        }
      },
      // 
      handleItemChange() {
        this.cpData = this.getCascaderObj(this.val, this.options);

        if (!this.cpData[this.cpData.length - 1].isOptional) {
          this.$alert('~', '', {
            confirmButtonText: '',
            type: 'warning',
            callback: action => {}
          });
          this.cpData = [];
        }
      },

      getCascaderObj(val, opt) {
        return val.map(function(value, index, array) {
          for (var itm of opt) {
            if (itm.id == value) {
              opt = itm.children;
              return itm;
            }
          }
          return null;
        });
      },
      // 
      handleUploadFile(data) {
        this.fwznfjMc = "";
        if (!!data.length) {
          this.demandData.xqfjwid = data[0].split("|")[0];
          this.demandData.xqfjmc = data[0].split("|")[1];
        } else {
          this.demandData.xqfjwid = "";
          this.demandData.xqfjmc = "";
        }
      },

      // crowd
      handleChangeCrowdYwx(val) {
        this.crowdywxList.forEach(ele => {
          if (ele.lbdm == val) {
            this.demandData.crowdywxmc = ele.lbmc;
          }
        });
        // 
        this.getCrowdCp(val);
      },

      // crowd
      handleChangeCrowdCp(val) {
        this.crowdcpList.forEach(ele => {
          if (ele.lbdm == val) {
            this.demandData.crowdcpmc = ele.lbmc;
          }
        });
      },

      // 
      handleEdit(data) {
        this.xmzt = data.xmzt;
        this.demandData.xmbh = data.xmbh;
        this.demandData.xmmc = data.xmmc;
        this.getUsers();
        this.dialogdemandVisible = false;
      },
      // 
      handleChooseDemand(data) {
        this.glxqMc = "[" + data.xqbh + "]" + data.bt;
        this.demandData.glxqWid = data.wid;
      },

      //
      adddemandtItem() {
        this.dialogdemandVisible = true;
      },
      // 
      handleRelaseDemand() {
        this.demandListVisible = true;
      },

      //
      handleCommit() {
        if (this.demandData.xqfl == 1) {
          this.demandData.glxqWid = "";
        }
        if (!!this.demandInfo.wid && this.Type.includes('edit')) {
          this.demandData.wid = this.demandInfo.wid;
        }
        if (!!this.btnbh) {
          this.demandData.btnbh = this.btnbh;
        }

        this.demandData.cpbjbh = !this.cpData.length ? '' : this.cpData[this.cpData.length - 1].id;
        this.demandData.cpbjmc = !this.cpData.length ? '' : this.cpData[this.cpData.length - 1].title;
        this.demandData.teacherData = this.teacherData.join("|");
        this.demandData.nr = $("#summernoteTT").summernote("code");

        if (!this.valiDate()) return;
        this.$post(this.API.submitDemand, this.demandData).then(res => {
          if (res.state == "success") {
            this.$message({
              message: "",
              type: "success"
            });
            $("#summernoteTT").summernote("code", "");
            this.isClearFile = !this.isClearFile;
            this.visible = false;
            this.$emit("handleCommitDemand", this.Type);
          } else {
            this.$message({
              message: res.msg,
              type: "warning"
            });
          }
        });
      },

      // 
      queryDemandProductTree(dosuccess) {
        this.xmcpList = [];
        this.$get(this.API.demandProductTree, {}).then(res => {
            if (res.state == "success") {
              this.options = res.data;
              dosuccess();
            } else {}
          }),
          // 
          this.$get(this.API.getCrowdYwx, {}).then(res => {
            if (res.state == "success") {
              this.crowdywxList = res.data;
            }
          });
      },

      // 
      getCrowdCp(ywxbm) {
        this.$get(this.API.getCrowdCp, {
          ywxbm: ywxbm
        }).then(res => {
          if (res.state == "success") {
            this.crowdcpList = res.data;
          }
        });
      },

      // 
      getUsers() {
        this.$get(this.API.getUsers, {
          curPage: 1,
          pageSize: 999,
          xmbh: this.demandData.xmbh,
          unitType: 1
        }).then(res => {
          if (res.state == "success") {
            this.tclsList = res.data.rows;
          } else {}
        });
      },

      // 
      getProjects() {
        getProjects({
          curPage: 1,
          pageSize: 2,
          keyword: "",
          xmlb: "",
          sfgx: "",
          xmzt: "1,2,3",
          pl: ""
        }).then(({
          data
        }) => {
          if (data.state == "success") {
            if (data.data.records == 1 && data.data.rows[0].xmzt != "") {
              this.xmzt = data.data.rows[0].xmzt;
              this.xmmc = data.data.rows[0].xmmc;
              this.xmbh = data.data.rows[0].xmbh;
            }
          }
        });
      },

      valiDate() {
        if (!this.demandData.xmbh) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.xqfl) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.teacherData) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.cpbjbh) {
          this.$message({
            message: "~",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.crowdywxbh) {
          this.$message({
            message: "CROWD",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.crowdcpbh) {
          this.$message({
            message: "CROWD",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.qwsjwcrq) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.qwkfjfrq) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (this.demandData.xqfl != 1 && !this.demandData.glxqWid) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.xqfjwid) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.bt) {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        if (!this.demandData.nr || this.demandData.nr == "<p><br></p>") {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
        return true;
      }
    },
    watch: {
      show() {
        this.visible = this.show;
        if (this.show) {
          this.$nextTick(() => {
            $("#summernoteTT").summernote({
              dialogsInBody: true,
              placeholder: "",
              focus: true,
              height: 200,
              width: 100 + "%",
              minHeight: 295,
              lang: "zh-CN",
              toolbar: [
                ["style", ["bold", "italic", "underline", "clear"]],
                ["font", ["strikethrough", "superscript", "subscript"]],
                ["fontsize", ["fontsize"]],
                ["color", ["color"]],
                ["para", ["ul", "ol", "paragraph"]],
                ["height", ["height"]],
                ["picture"],
                ["link", ["linkDialogShow", "unlink"]]
              ]
            });
            if (this.Type == "edit" || this.Type == "editDisabled") {
              $("#summernoteTT").summernote("code", this.demandInfo.nr);
            }
          });

          //
          if (!getSession("DemandType")) {
            getMenu("DemandType", this.xqflList); //
          } else {
            this.xqflList = getSession("DemandType");
          }
          this.queryDemandProductTree(() => {
            if (this.Type == "edit" || this.Type == "editDisabled") {
              this.val = this.demandInfo.syfjcpbh.split(',').reverse();
              this.cpData = this.getCascaderObj(this.val, this.options);
            }
          });
          this.teacherData = [];
          // 
          if (this.Type == "edit" || this.Type == "editDisabled") {
            $("#summernoteTT").summernote("code", this.demandInfo.nr);
            this.xmzt = this.demandInfo.xmzt;
            this.demandData.xmbh = this.demandInfo.xmbh;
            this.demandData.xmmc = this.demandInfo.xmmc;
            this.demandData.xqfl = this.Type == "editDisabled" ? '3' : this.demandInfo.xqfl;
            this.demandData.sfsx = this.demandInfo.sfsx;
            this.demandData.cpbjbh = this.demandInfo.cpbjbh;
            this.demandData.cpbjmc = this.demandInfo.cpbjmc;
            this.demandData.crowdywxbh = this.demandInfo.crowdywxbh;
            this.demandData.crowdywxmc = this.demandInfo.crowdywxmc;
            this.getCrowdCp(this.demandInfo.crowdywxbh);
            this.demandData.crowdcpbh = this.demandInfo.crowdcpbh;
            this.demandData.crowdcpmc = this.demandInfo.crowdcpmc;
            this.demandData.qwsjwcrq = this.demandInfo.qwsjwcrq;
            this.demandData.qwkfjfrq = this.demandInfo.qwkfjfrq;
            this.demandData.bt = this.demandInfo.bt;
            this.demandData.xqfjwid = this.demandInfo.xqfjwid;
            this.demandData.xqfjmc = this.demandInfo.xqfjmc;
            this.fwznfjMc = this.demandInfo.xqfjmc;
            this.glxqMc = !this.demandInfo.glxqbh ? '' : ("[" + this.demandInfo.glxqbh + "]" + this.demandInfo.glxqbt); //
            this.demandData.glxqWid = this.demandInfo.glxqwid; //wid
            this.getUsers();
            let tclsbh = this.demandInfo.tclsbh.split(',');
            let tclsxm = this.demandInfo.tcls.split(',');

            tclsbh.forEach((ele, i, arr) => {
              this.teacherData.push(tclsbh[i] + ',' + tclsxm[i])
            })

          }
          // 
          if (this.Type == "disabled") {
            this.demandData.xqfl = '3';
            this.xmzt = this.demandInfo.xmzt;
            this.demandData.xmbh = this.demandInfo.xmbh;
            this.demandData.xmmc = this.demandInfo.xmmc;
            this.glxqMc = "[" + this.demandInfo.xqbh + "]" + this.demandInfo.bt; //
            this.demandData.glxqWid = this.demandInfo.wid; //wid
            this.getUsers();
          }

        } else {
          // $('#summernoteTT').summernote('destroy');
          this.xmzt = this.glxqMc = "";
          this.demandData.cpbjbh = "";
          this.demandData.teacherData = "";
          this.demandData.xmbh = "";
          this.demandData.xmmc = "";
          this.demandData.xqfl = "";
          this.demandData.teacherData = "";
          this.demandData.sfsx = "0";
          this.demandData.cpbjbh = "";
          this.demandData.cpbjmc = "";
          this.demandData.crowdywxbh = ""; //crowd 
          this.demandData.crowdywxmc = ""; //crowd 
          this.demandData.crowdcpbh = ""; //crowd 
          this.demandData.crowdcpmc = ""; //crowd 
          this.demandData.qwsjwcrq = "";
          this.demandData.qwkfjfrq = "";
          this.demandData.glxqWid = "";
          this.demandData.bt = "";
          this.demandData.xqfjwid = "";
          this.demandData.xqfjmc = "";
        }
      }
    }
  };
</script>
<style scoped lang="scss">
  .demand {
    padding: 10px 0;
  }

  div.el-form-item {
    margin-bottom: 8px !important;
  }

  .demand-textarea {
    width: 950px;
    margin: 0 auto;
  }

  .demand-textarea>p {
    width: 125px;
    text-align: right;
    padding-right: 12px;
    font-weight: 700;
  }

  .demand-textarea>p::before {
    content: "*";
    color: #f56c6c;
    margin-right: 4px;
  }

  .file {
    padding: 2px 6px;
    border-radius: 3px;
    margin-top: 4px !important;

    &:hover {
      background: rgba(216, 214, 214, 0.5);
    }

    i {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      text-align: center;
      line-height: 20px;

      &:hover {
        color: #f00;
        cursor: pointer;
        background: rgba(255, 0, 0, 0.25);
      }
    }
  }

  .cptree {
    position: absolute;
    top: 0;
    background: #fff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
    border: 1px solid #E4E7ED;
    border-radius: 4px;
    top: 40px;
    z-index: 10200;
    width: 325px;
    max-height: 400px;
    overflow-y: auto;
    overflow-x: auto;
  }
</style>



