<template>
  <div>
    <el-dialog :title="Type == 'lstd'?'':'crowd'" width="1000px" top="30px" :visible.sync="visible" :append-to-body="true"
      :close-on-click-modal="false" @close="$emit('update:show', false)" :show="show">
      <div class="pd-10">
        <el-form :model="crowdxqData" :rules="rules" ref="crowdxqData" class="demo-ruleForm">
          <el-form-item>
            <div class="send-crowd_topic"></div>
          </el-form-item>
          <el-form-item prop="rwmc">
            <el-input size="mini" v-model="crowdxqData.rwmc" placeholder=""></el-input>
          </el-form-item>

          <el-form-item >
            <el-select size="mini" v-model="crowdxqData.sfjj" placeholder="" style="width: 100%;">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item >
            <el-select size="mini" v-model="crowdxqData.sfxyzc" placeholder="" style="width: 100%;">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <!--  -->
          <el-form-item>
            <el-input size="mini" v-model="crowdxqData.xmysje" :readonly="crowdxqData.rwlx == '2'" placeholder="()"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item>
            <el-input type="textarea" placeholder=" , ," v-model="crowdxqData.jmxx"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item prop="xqms">
            <el-input type="textarea" placeholder="" v-model="crowdxqData.xqms"></el-input>
          </el-form-item>

    <!--      <el-form-item >
            <uploadFile :isCrowd="true" @handleUploadFile="handleUploadFile"></uploadFile>
          </el-form-item> -->

          <el-form-item >
            <div class="send-crowd_topic">//</div>
          </el-form-item>
          <el-form-item >
            <el-date-picker :picker-options="pickerJfrqDateBefore" value-format="yyyy-MM-dd" size="mini" type="date"
              placeholder=" " v-model="crowdxqData.qwjfrq" style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <div class="send-crowd_topic" flex spacebetween>
              <span>/</span>
              <a href="javaScript:;;" @click="handleCheckDateRule"></a>
            </div>
          </el-form-item>
          <el-form-item>
            <el-date-picker @change="handleChoosejfDate" value-format="yyyy-MM-dd" :picker-options="pickerJfrqDateBefore"
              size="mini" type="date" placeholder="" v-model="crowdxqData.zbjzrq"
              style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-date-picker @change="handleChoosejfDate2" value-format="yyyy-MM-dd" :picker-options="pickerJfrqDateBefore"
              size="mini" type="date" placeholder="" v-model="crowdxqData.jfrq" style="width: 100%;"></el-date-picker>
          </el-form-item>

          <el-form-item text-right>
            <el-button size="mini" type="primary" @click="sendSubmitForm('crowdxqData')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dateruleVisible" :close-on-click-modal="false" width="600px" top="30px"
      append-to-body>
      <div style="padding:20px 10px" class="crowd-rqgz">
        <h4> </h4>
        <p>1</p>
        <p>2</p>
        <br />
        <h4> </h4>
        <p>
          11200212005,1200212003
        </p>
        <p>2BUGBUG1BUG2424</p>
        <br />
        <h4> </h4>
        <p>
          
        </p>
        <br />
        <h4> </h4>
        <p>1</p>
        <p>2</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // import uploadFile from '@/components/BusinessPage/upload.vue';
  import {
    getMenu,
    getSession,
    GetDateStr
  } from '@/utils/util.js';
  import { queryUser } from "@/api/personal.js";

  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (!/^([1-9]\d*(\.\d*[1-9])?)|(0\.\d*[1-9])$/.test(value) && crowdxqData.rwlx != '2') {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        dateruleVisible: false, //
        visible: this.show,
        pickerJfrqDateBefore: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        loading:false,//loading
        kfgcsData: [], //
        kfgcsList:[],//
        options: [{
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: 'BUG'
          }
        ],

        rules: {
          rwmc: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          xmysje: [{
            validator: validatePass,
            trigger: 'blur'
          }],
          zbjzrq: [{
            type: 'date',
            required: true,
            message: '',
            trigger: 'change'
          }],
          jfrq: [{
            type: 'date',
            required: true,
            message: '',
            trigger: 'change'
          }],
          qwjfrq: [{
            type: 'date',
            required: true,
            message: '',
            trigger: 'change'
          }],
          xqms: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        crowdxqData: {
          rwmc: '', //
          sfjj: '0', //
          sfxyzc: '0', //
          xmysje: 0,
          jmxx: '',
          xqms: '', //
          zbjzrq: '',
          jfrq: '',
          qwjfrq: ''
        },
      }
    },
    methods: {
      handleClose() {
        this.visible = false
      },

      // 
      handleSeleteYwx(val) {
        this.getCrowdCp(val);
      },
      // 1
      handleChoosejfDate(val) {
        if (val > this.crowdxqData.jfrq) {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'warning'
          });
          this.crowdxqData.zbjzrq = '';
        }
      },
      // 
      handleChoosejfDate2(val) {
        if (val < this.crowdxqData.zbjzrq) {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'warning'
          });
          this.crowdxqData.jfrq = '';
        }
      },
      // 
      handleCheckDateRule() {
        this.dateruleVisible = true;
      },
      // 
      sendSubmitForm(formName) {
        if (!this.crowdxqData.jfrq || !this.crowdxqData.zbjzrq) {
          this.$alert('/', '', {
            confirmButtonText: '',
            type: 'warning'
          });
          return;
        }
        if (this.crowdxqData.rwlx != '2' && !/^([1-9]\d*(\.\d*[1-9])?)|(0\.\d*[1-9])$/.test(this.crowdxqData.xmysje)) {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'warning'
          });
          return;
        }
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.crowdxqData.zbwid = this.zbwid;
            this.crowdxqData.btnbh = this.btnbh;
            this.$post(!this.Type?this.API.projectTeamSubmitDvpt:this.API.openGreenChannel,this.crowdxqData).then(res=>{
              if(res.state == 'success'){
                  this.$message({message:'',type:'success'});
                  this.$emit('handleCommitCrowd','');
                  this.crowdxqData.rwmc = '';
                  this.crowdxqData.sfjj = '0';
                  this.crowdxqData.sfxyzc = '0';
                  this.crowdxqData.xmysje = 0;
                  this.crowdxqData.jmxx = '';
                  this.crowdxqData.xqms = '';
                  this.crowdxqData.zbjzrq = '';
                  this.crowdxqData.jfrq = '';
                  this.crowdxqData.qwjfrq = '';
                  this.visible = false;

              }else{
                  this.$message({message:res.msg,type:'error'});
              }
            })


          } else {
            return false;
          }
        });
      },

      // 
      getCrowdYwx() {
        this.$get(this.API.getCrowdYwx, {}).then(res => {
          if (res.state == 'success') {
            this.CrowdYwx = res.data;
          }
        });
      },
      // 
      getCrowdCp(ywxbm) {
        this.$get(this.API.getCrowdCp, {
          ywxbm: ywxbm
        }).then(res => {
          if (res.state == 'success') {
            this.CrowdCp = res.data;
          }
        });
      },
      // 
      remoteMethod(query) {
        if (query !== '') {
          this.loading = true;
          this.queryUser(query);
        } else {
          this.kfgcsList = [];
        }
      },
      // 
      queryUser(keyword) {
      	queryUser({
      		curPage: 1,
      		pageSize: 10,
      		Isexternal: false,
      		keyword: keyword
      	}).then(({ data }) => {
      		if (data.state == 'success') {
      			this.kfgcsList = data.data.rows;
            this.loading = false;
      		}
      	});
      }
    },
    props: {
      show: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: 'Crwod'
      },
      Type:{
        type:String,
        default:''
      },
      zbwid:{
        type:String,
        default:''
      },
      btnbh:{
        type:String,
        default:''
      }
    },
    watch: {
      show(n, o) {
        this.visible = this.show;
        if (this.show) {
          this.crowdxqData.zbjzrq = GetDateStr(7);
          this.getCrowdYwx();
          this.queryUser();
          
        }
      }
    },
    components: {}
  }
</script>

<style scoped>
  .send_crowd img {
    width: 100%;
  }

  .send-crowd_topic {
    border: 1px solid #e4eaec;
    background: #f3f7f9;
    border-radius: 3px;
    height: 40px;
    line-height: 40px;
    padding: 0 6px;
  }

  .crowd-rqgz h4 {
    font-weight: 700;
    font-size: 13px;
    margin: 10px 0 !important;
  }

  .crowd-rqgz p {
    font-size: 12px;
    text-indent: 2em;
  }
</style>



